#left-menu{
    position: fixed;
    top: 500px;
    transform: rotate(-90deg);
    left: -165px;
}

.left-menu__content{
    display: flex;
    align-items: center;
}

.left-menu__arrow{
    display: flex;
    align-items: center;

    i{
        text-align: center;
        font-size: 14px;
    }
}

.left-menu__list{
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
}

.left-menu__link{
    text-transform: uppercase;
    margin-left: 30px;
    color: $purple;
    font-weight: 800;
}